.movies {
  display: flex;
  justify-content: space-between;
  align-items: flex-start;
  flex-wrap: wrap;
  padding: 50px;
  width: 80%;
  padding-top: 70px;
}

.movies .movie {
  width: 45%;
  background-color: aliceblue;
  margin-bottom: 70px;
  display: flex;
  align-items: flex-start;
  justify-content: space-between;
  font-weight: 300;
  padding: 20px;
  border-radius: 5px;
  color: #adaeb9;
  box-shadow: 0 13px 27px -5px rgba(50, 50, 93, 0.25),
    0 8px 16px -8px rgba(0, 0, 0, 0.3), 0 -6px 16px -6px rgba(0, 0, 0, 0.025);
}


.movie img {
  position: relative;
  top: -50px;
  max-width: 150px;
  width: 100%;
  margin-right: 30px;
  box-shadow: 0 13px 27px -5px rgba(50, 50, 93, 0.25),
    0 18px 36px -18px rgba(0, 0, 0, 0.3), 0 -12px 36px -8px rgba(0, 0, 0, 0.025);
}

.movie .movie_title,
.movie .movie_release {
  font-weight: 300;
  margin: 0;
}

.movie .movie_title {
  margin-bottom: 5px;
  font-size: 24px;
  color: #2c2c2c;
  font-weight: 500;
}

.movie_voteCount, .movie_release, .movie_overview  {
  color: #b2b3bc;
}

.movie_overview {
  font-size: 13px;
}